*{
  box-sizing: border-box;
}

body{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:rgba(0, 0, 0, 0.8);
}

.btn{
  background-color: white;
  color: yellow;

  font-family: "Monteserrat", sans-serif ;
  text-transform: uppercase;
  margin: 15px;
  /* padding: 25px; */
  border: 2px solid yellow;
  border-radius: 10px;
  box-shadow: 0 12px rgb(184, 184, 64), 0 10px rgba(0, 0, 0, 0.4);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  /* position: relative; */
  top: 0;
  
}

  .btn:hover {
    background-color: yellow;
    color: black;
    /* top: 4px; */
    box-shadow: 0.5px rgb(184, 184, 64), 0 15px rgba(0, 0, 0, 0.9);
    /* animation: gelatine 0.8s infinite; */
  }
